import React from "react";
import styled,{css} from "styled-components";

const ColumnWrap = css`
    display:flex;
    flex-direction:column;
    align-items:center;
    width:fit-content;
    padding: 0 1rem;
`
const Text=styled.span`
    font-size:${props=>props.size||1}rem;
    ${props=>{
        if(props.bold){return css`font-weight:bold;`;}
        else if(props.light){return css`font-weight:lighter`;}
    }}
`;
const TextCss=css`
    font-size:${props=>props.size||1}rem;
    ${props=>{
        if(props.bold){return css`font-weight:bold;`;}
        else if(props.light){return css`font-weight:lighter`;}
    }}
`;

export {
    ColumnWrap,
    Text,
    TextCss
}
